import React, {Component} from 'react';
import styles from './styles.module.css';
import Navbar from '../Navbar/Navbar';
import axios from "axios"; 

const api = axios.create({
    baseURL: `https://nmfbloancalculator.azurewebsites.net/api/post/getCal.php`
})


class NonBenficiary extends Component {
    constructor(){
        super();     
        this.state = {
            loan: '',
            year: '', 
      }
      
    }
    state = {
        cal: []
    }
  
  handleSubmit = event => {
      event.preventDefault();
      api.get('', {
        params: {
            loan: this.state.loan,
            year: this.state.year
        }
      }).then(res =>{
       // const cal = res.data;
        //this.setState({ cal });
        window.location.href = '/view';
        //console.log(res.data)
        localStorage.setItem("data", JSON.stringify(res.data));
    })
 
    }
render() {
    return(
        <div className={styles.main}>
            <div className={styles.grid}>
                <div className={styles.slides}>
                    <Navbar />
                    <div className={styles.img}>
                    </div>
                </div>
                <div className={styles.slides2}>
                    <h1>Loan Calculator</h1>
                    <h2>Enter Your Loan Information to Calculate your Loan</h2>
              
                 <form onSubmit = { this.handleSubmit }> 
                <div className={styles.inputGroup}>
                    <input type="number" name="loan" placeholder="250,000" value={this.state.loan} onChange={event => this.setState({ loan: event.target.value })} />
                    <label>Enter Loan Amount [i.e 250,000]</label>
                    </div>
                    <div className={styles.inputGroup}>
                    <input type="number" name="year" placeholder="2 Years" value={this.state.year} onChange={event => this.setState({ year: event.target.value })} />
                    <label>Loan Term [i.e 2 Years]</label>
                    </div>
                
                    <div className={`${styles.inputGroup} ${styles.formBtn}` }>
                    <input type="submit" name="submit" value="Calculate Loan" className={styles.btn} />
                    </div>
             
                 </form>
                </div>
            </div>
        </div>
    )
}
}
export default NonBenficiary;