import React from 'react';
import styles from './styles.module.css';
import Navbar from '../Navbar/Navbar';
import { Link } from 'react-router-dom';

const CBNlist = () => {

    return(
        <div className={styles.main}>
            <div className={styles.grid}>
                <div className={styles.slides}>
                    <Navbar />
                    <div className={styles.img}>
                    </div>
                </div>
                <div className={styles.slides2}>
                    <h1>Loan Calculator</h1>
                    <h2>CBN Intervention</h2>
                    <Link to ="/Covid19" className={styles.btn}>COVID19 TCF</Link>
                    <Link to ="/ABP" className={styles.btn}>ABP</Link>
                    <Link to ="/AGSMEIS" className={styles.btn}>AGSMEIS</Link>
                    <Link to ="/NYIF" className={styles.btn}>NYIF</Link>
              
             
                </div>
            </div>
        </div>
    )
}
export default CBNlist;