import React from 'react'
import {BrowserRouter as Router, Switch, Route} from 'react-router-dom';
import Home from './components/pages/Home';
import Page2 from './components/pages/NonBenficiary';
import Page3 from './components/pages/View';
import Page4 from './components/pages/Benficiary';
import CBN from './components/pages/CBN';
import Covid19 from './components/pages/Covid19';
import Views from './components/pages/Views';
import Households from './components/pages/Household';
import SMEs from './components/pages/SME';
function App() {
  return (
    <Router>
   
    <Switch>
        <Route path='/' exact component={Home} />  
        <Route path='/NonBenficiary' exact component={Page2} />  
        <Route path='/Benficiary' exact component={Page4} />  
        <Route path='/view' exact component={Page3} />  
        <Route path='/CBN' exact component={CBN} />  
        <Route path='/Covid19' exact component={Covid19} />  
        <Route path='/Views' exact component={Views} />  
        <Route path='/SME' exact component={SMEs} />  
        <Route path='/Household' exact component={Households} />  
       
       </Switch>
     </Router>
  );
}

export default App;
