import React, {Component} from 'react';
import styles from './styles.module.css';
import Navbar from '../Navbar/Navbar';
import axios from "axios"; 

const api = axios.create({
    baseURL: `https://nmfbloancalculator.azurewebsites.net/api/post/getUser.php`
})


class Household extends Component {
    constructor(){
        super();     
        this.state = {
            bvn: ''
      }
      
    }
    state = {
        cal: []
    }
  
  handleSubmit = event => {
      event.preventDefault();
      api.get('', {
        params: {
            bvn: this.state.bvn
        }
      }).then(res =>{
       // const cal = res.data;
        //this.setState({ cal });
        window.location.href = '/Views';
        //console.log(res.data)
        localStorage.setItem("data", JSON.stringify(res.data));
    })
 
    }
render() {
    return(
        <div className={styles.main}>
            <div className={styles.grid}>
                <div className={styles.slides}>
                    <Navbar />
                    <div className={styles.img}>
                    </div>
                </div>
                <div className={styles.slides2}>
                    <h1>Loan Calculator</h1>
                    <h2>COVID19 TCF</h2>

                 <form onSubmit = { this.handleSubmit }> 
                <div className={styles.inputGroup}>
                    <input type="number" name="bvn" placeholder="250,000" value={this.state.bvn} onChange={event => this.setState({ bvn: event.target.value })} required />
                    <label>Enter your BVN [i.e 222*********]</label>
                    </div>
                    <div className="">
                    <input type="submit" name="submit" value="Calculate Loan" className={styles.btn} />
                    </div>
             
                 </form>
                </div>
            </div>
        </div>
    )
}
}
export default Household;