import React from 'react';
import styles from './styles.module.css';
import Navbar from '../Navbar/Navbar';
import { Link } from 'react-router-dom';

const Benficiary = () => {

    return(
        <div className={styles.main}>
            <div className={styles.grid}>
                <div className={styles.slides}>
                    <Navbar />
                    <div className={styles.img}>
                    </div>
                </div>
                <div className={styles.slides2}>
                    <h1>Loan Calculator</h1>
                    <h2>Select Product type </h2>
                    <Link to ="/CBN" className={styles.btn}>CBN Intervention (COVID19 TCF, ABP, AGSMEIS and NYIF e.t.c)</Link>
                    <Link to ="/OBS" className={styles.btn}>On-balance sheet (SALAD, Petty Now Now, SME Plus, etc)</Link>
              
             
                </div>
            </div>
        </div>
    )
}
export default Benficiary;