import React, {Component} from 'react';
import styles from './styles2.module.css';
import Navbar from '../Navbar/Navbar';
import { getData } from '../Utility/index';

class Views extends Component {
    constructor(){
        super();     
        this.state = {
            data:{
               
            }
      }
    }
      async componentDidMount() {
        console.log(await getData());
        this.setState({ data: JSON.parse(await getData()) });
        }

render(){
    return(
        <div className={styles.main}>
            <div className={styles.grid}>
                <div className={styles.slides}>
                    <Navbar />
                    <div className={styles.img}>
                    </div>
                </div>
                <div className={styles.slides2}>
                    <h1>Loan Calculator Results</h1>
                <div className={styles.igrid}>
                <div className={styles.inputGroup}>
                    <input type="number" name="loan" placeholder="250,000" value={this.state.data.ApprovedLoanAmount} onChange={event => this.setState({ loan: event.target.value })} />
                    <label>Loan Amount [i.e 250,000]</label>
                    </div>
                    <div className={styles.inputGroup}>
                    <input type="number" name="year" placeholder="2 Years" value={this.state.data.Intrest} onChange={event => this.setState({ year: event.target.value })} />
                    <label>Total Interest</label>
                    </div>
                    <div className={styles.inputGroup}>
                    <input type="number" name="upfront" placeholder="2 Years" value={this.state.data.upfront} onChange={event => this.setState({ year: event.target.value })} />
                    <label>4% Upfront Charge</label>
                    </div>
                    <div className={styles.inputGroup}>
                    <input type="number" name="upfront" placeholder="2 Years" value={this.state.data.TotalAmount} onChange={event => this.setState({ year: event.target.value })} />
                    <label>Total Interest After upfront charge</label>
                    </div>
                    <div className={styles.inputGroup}>
                    <input type="number" name="upfront" placeholder="2 Years" value={this.state.data.MonthlyAmount} onChange={event => this.setState({ year: event.target.value })} />
                    <label>Monthly Amount Due</label>
                    </div>
                
                    {/* <div className={`${styles.inputGroup} ${styles.formBtn}` }>
                    <input type="submit" name="submit" value="Calculate Loan" className={styles.btn} />
                    </div> */}
                    </div>
             
           
                </div>
            </div>
        </div>

    )
}
}

export default Views;